
import { Mail } from "@material-ui/icons"
import {Facebook , Instagram , Twitter , Pinterest} from "@mui/icons-material"
import styled from "styled-components"
import {Room , Phone , MailOutline} from "@material-ui/icons"
import {mobile} from "../Responsive"

const Container = styled.div` 
    display:flex;
    ${mobile({flexDirection:"column"})};
`

const Left = styled.div` 
    flex:1;
    display:flex;
    flex-direction:column;
    padding:20px;
`
const Logo = styled.h1`

`

const Desc = styled.p`
    margin:20px 0px;

`

const SocialContainer = styled.div`
    display:flex;
`

const SocialIcon = styled.div`
    width:40px;
    height:40px;
    border-radius:50%;
    color:white;
    background-color:#${props=>props.color};
    display:flex;
    align-items:center;
    justify-content:center;
    margin-right:20px;
`

const Center= styled.div` 
flex:1;
padding:20px;
${mobile({display:"none"})};
`
const Title = styled.h3`
margin-bottom:30px;
`
const List = styled.ul`
margin:0;
padding:0;
list-style:none;
display:flex;
flex-wrap:wrap;
`
const ListItem = styled.li`
width:50%;
margin-bottom:10px;
`

const Right = styled.div` 
flex:1;
padding:20px;
${mobile({backgroundColor:"#fff8f8"})};
`
const ContactItem = styled.div`
margin-bottom:20px;
display:flex;
align-items:center;
`
const Payment = styled.img`
width:100%;
height:50px;
`

const Footer = () => {
  return (
    <Container>
        <Left>
            <Logo>ShopFun.</Logo>
            <Desc>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Doloribus nihil illum odit repudiandae perferendis exercitationem consectetur nesciunt, quo ipsum molestiae sint ipsam provident at impedit magnam quaerat quia. Sed, reiciendis.</Desc>
            <SocialContainer>
                <SocialIcon color="385999">
                    <Facebook/>
                </SocialIcon>
                <SocialIcon color="E4405F">
                    <Instagram/>
                </SocialIcon>
                <SocialIcon color="55ACEE">
                    <Twitter/>
                </SocialIcon>
                <SocialIcon color="E6023D">
                    <Pinterest/>
                </SocialIcon>
            </SocialContainer>
        </Left>
        <Center>
            <Title>Links you may need</Title>
            <List>
                <ListItem>
                    Home
                </ListItem>
                <ListItem>
                    Kidswear
                </ListItem>
                <ListItem>
                    Men's Fashion
                </ListItem>
                <ListItem>
                    Women's Fashion
                </ListItem>
                <ListItem>
                    Accessories
                </ListItem>
                <ListItem>
                    Cart
                </ListItem>
                <ListItem>
                    My Account
                </ListItem>
                <ListItem>
                    Order Tracking
                </ListItem>
                <ListItem>
                    My Wishlist
                </ListItem>
                <ListItem>
                    Terms and Conditions
                </ListItem>
            </List>
        </Center>
        <Right>
            <Title>
                Contact
            </Title>
            <ContactItem>
                <Room style={{marginRight:"10px"}}/>
                634 Tinsel Street , London 983367
            </ContactItem>
            <ContactItem>
                <Phone style={{marginRight:"10px"}}/>
                +1 123 456 789 1
            </ContactItem>
            <ContactItem>
                <MailOutline style={{marginRight:"10px"}}/>
                contact@shopfun.com
            </ContactItem>
            <Payment src="https://www.mtbonnell.com/wp-content/uploads/2020/04/payment-methods_01.jpg"/>
        </Right>
    </Container>
  )
}

export default Footer
