import styled from "styled-components"
import {mobile} from "../Responsive"

const Container = styled.div`
width:100vw;
height:100vh;
background: url("https://www.businessmagazine.org/wp-content/uploads/2021/12/Shopping.jpg") 
 center;
 background-repeat:no-repeat;
 background-size:cover;
 display:flex;
 align-items:center;
 justify-content:center;
`

const Wrapper = styled.div`
padding:20px;
width:40%;
opacity:0.9;
background-color:white;
${mobile({width:"75%"})};
`

const Title = styled.h1`
font-size:24px;
font-weight:300;
`

const Form = styled.div`
display:flex;
flex-wrap:wrap;
`

const Input = styled.input`
flex:1;
min-width:40%;
margin:20px 10px 0px 0px;
padding:10px;
`

const Agreement = styled.span`
font-size:12px;
margin:20px 0px;

`

const Button = styled.button`
width:40%;
border:none;
padding:15px 20px;
background-color:teal;
color:white;
cursor:pointer;
`
const Register = () => {
  return (
    <Container>
        <Wrapper>
            <Title>CREATE AN ACCOUNT</Title>
            <Form>
                <Input placeholder="First Name"/>
                <Input placeholder="Last Name"/>
                <Input placeholder="Username"/>
                <Input placeholder="Email"/>
                <Input placeholder="Password"/>
                <Input placeholder="Confirm Password"/>
                <Agreement>by creating an account, I agree to the give access to my personal information as per the <b>PRIVACY POLICY</b></Agreement>
                <Button>
                    CREATE
                </Button>
            </Form>
        </Wrapper>
    </Container>
  )
}

export default Register
