import styled from "styled-components"

const Container = styled.div`
    height:30px;
    background-color:lightblue;
    color:black;
    display:flex;
    align-items:center;
    justify-content:center;
    font-size:14px;
    font-weight:bolder;
`
const Announcements = () => {
  return (
    <Container>
        Big Billion Days are here! Free shipping on orders over $150
    </Container>
  )
}

export default Announcements
